<template lang="html">
  <section class="cards">
    <!-- ALERT -->
    <v-alert v-if="error" :dismissible="false" :type="'error'" class="mb-4 mt-12">{{this.errorMessage}}</v-alert>
    <!-- LOADER -->
    <v-progress-linear :active="this.loader" :background-opacity="0" :height="10" :indeterminate="true" :top="true" :value="40" color="blue"></v-progress-linear>
    <!-- MOVIES -->
    <v-flex wrap row justify-center class="flex-wrap" v-if="this.currentCategorie !== 'person/popular'">
      <v-card class="v-card-movie" v-for="movie in movies" width="500" height="280px" outlined @click="changeHashAndOpenMovieOrPerson(movie.id, movie.name, movie.title)">
        <v-layout column flex-sm-row>
          <v-flex>
            <!-- IMAGE DESKTOP -->
            <v-img class="movie_image" v-if="movie.poster_path" height="278" width="185" :src="`https://image.tmdb.org/t/p/w300_and_h450_bestv2${movie.poster_path}`"></v-img>
            <v-img class="movie_image" v-if="!movie.poster_path" height="278" width="185" src="../assets/noImageAvailable.jpg"></v-img>
            <!-- IMAGE MOBILE -->
            <v-img class="movie_image_background" v-if="movie.poster_path" height="300" width="408" :src="`https://image.tmdb.org/t/p/original${movie.backdrop_path}`"></v-img>
            <v-img class="movie_image_background" v-if="!movie.poster_path" height="300" width="408" src="../assets/noImageAvailable.jpg"></v-img>
          </v-flex>
          <v-flex>
            <v-flex row justify-start flex-sm-center> 
              <div v-if="movie.vote_average*10 >= 0">
                <v-progress-circular v-if="movie.vote_average*10 >= 70" :rotate="-90" :size="50" :width="7" :value="movie.vote_average*10" color="green">{{ movie.vote_average*10 }}</v-progress-circular>
                <v-progress-circular v-if="movie.vote_average*10 >= 50 && movie.vote_average*10 < 70" :rotate="-90" :size="50" :width="7" :value="movie.vote_average*10" color="orange">{{ movie.vote_average*10 }}</v-progress-circular>
                <v-progress-circular v-if="movie.vote_average*10 < 50" :rotate="-90" :size="50" :width="7" :value="movie.vote_average*10" color="red">{{ movie.vote_average*10 }}</v-progress-circular>
              </div>
              <div class="card-title">
                <v-card-title v-if="movie.name" class="card-title">{{ movie.name }}</v-card-title>
                <v-card-title v-if="movie.title" class="card-title">{{ movie.title }}</v-card-title>
                <v-card-text v-if="movie.release_date" class="release_date">{{ movie.release_date }}</v-card-text>
                <v-card-text v-if="movie.first_air_date" class="release_date">{{ movie.first_air_date }}</v-card-text>
              </div>
            </v-flex>
            <v-card-text class="overview">{{ movie.overview }}</v-card-text>
            <v-card-text v-if="movie.known_for" v-for="k in movie.known_for" class="overview">{{ k.title }}</v-card-text>
          </v-flex>
        </v-layout>
      </v-card>
      <v-pagination v-model="currentPageIndex" :length="this.total_pages" class="my-10" total-visible="10"></v-pagination>
    </v-flex>
    <!-- PEOPLE -->
    <v-flex wrap row justify-center class="flex-wrap" v-if="this.currentCategorie == 'person/popular'">
      <v-card class="v-card-people" v-for="movie in movies" width="235" height="330px" outlined @click="changeHashAndOpenMovieOrPerson(movie.id, movie.name)">
        <v-layout>
          <v-flex column justify-center>
            <v-img  class="people_image" v-if="movie.profile_path" height="235" width="235" :src="`https://image.tmdb.org/t/p/w300_and_h300_bestv2${movie.profile_path}`"></v-img>
            <v-img class="people_image" v-if="!movie.profile_path" height="235" width="235" src="../assets/noPhotoAvailable.png"></v-img>
              <div class="card-title-people mt-n4 mb-n4">
                <v-card-title v-if="movie.name">{{ movie.name }}</v-card-title>
              </div>
            <v-card-text v-if="movie.known_for" v-for="k in movie.known_for" class="overview">
              <span v-if="k">{{ k.title }}</span>
            </v-card-text>
          </v-flex>
        </v-layout>
      </v-card>
      <v-pagination v-model="currentPageIndex" :length="this.total_pages" class="my-10" total-visible="10"></v-pagination>
    </v-flex>
  </section>
</template>

<script lang="js">
  import { mapState } from "vuex";
  export default  {
    name: 'cards',
    props: [],
    mounted() {

    },
    data() {
      return {
        currentPageIndex: +this.$route.params.page,
      }
    },
    methods: {
      changeHashAndOpenMovieOrPerson(id, name, title) { 
        this.$store.dispatch('removeData');
        let movieName = '';
        if (name) {
          movieName = name.replace(/ /g, '-').toLowerCase();
        } else {
          movieName = title.replace(/ /g, '-').toLowerCase();
        }

        if(this.currentCategorie !== 'person/popular') {
          this.$router.push({path:`/movie/${this.currentCategorie.replace('/','-')};${movieName};${id};${this.language};`});
        } else {
          this.$router.push({path:`/person/${this.currentCategorie.replace('/','-')};${movieName};${id};${this.language};`});
        }
      },
    },
    computed: {
      ...mapState([
        'movies',
        'total_pages',
        'currentCategorie',
        'currentPage',
        'language',
        'error',
        'errorMessage',
        'loader',
      ]),
    },
    watch: {
      currentPageIndex: function (newIndex) {
        this.currentPageIndex = newIndex;
        this.$router.push({ path:`/${this.currentCategorie};page=${newIndex};${this.language};`});
        this.$store.dispatch('removeData');
        this.$store.dispatch('getJSON',{categorie: `${this.currentCategorie}`,id:'',page:`${newIndex}`,language:`${this.language}`});
      },
    },
}
</script>

